import uniRate from "../../components/uni-rate/uni-rate.vue"
export default {
	components: {
		uniRate
	},
	props: {},
	data() {
		return {
			list: [],
		}
	},
	methods: {
		re() {
			uni.request({
				url: `${this.$api}/index/movie/hot?qq=122212489&type=superhero`,
				method: 'POST',
				data: {},
				success: res => {
					this.list = res.data.data
					// console.log(this.list);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		jupe(item,index){
			uni.navigateTo({
				url:`../details/details?item=${JSON.stringify(item)}`
			})
		}
	},
	mounted() {
		this.re()
	}
}
